.header {
  margin-top: 200px; }

.bars {
  height: 5px;
  text-align: center;
  width: 100%; }

.bars ul {
  display: flex;
  margin: 0;
  padding: 0; }

.bars ul li {
  list-style: none;
  height: 5px;
  width: 20%; }

.bars .cor-1 {
  background: #ED5565; }

.bars .cor-2 {
  background: #f59120; }

.bars .cor-3 {
  background: #FCBB42; }

.bars .cor-4 {
  background: #94c23d; }

.bars .cor-5 {
  background-color: #3498db; }

#faq {
  margin-top: 200px;
  /* CSS used here will be applied after bootstrap.css */ }
  #faq .header-title {
    background: #373746;
    height: 200px;
    color: #efefef;
    width: 100%;
    text-align: center; }
    #faq .header-title h2 {
      padding: 70px 0; }
  #faq .main-faq {
    height: 85vh;
    background: white; }
  #faq .jumbotron {
    width: 80%;
    background: white;
    margin-left: 10%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto; }
  #faq .img-center {
    margin: 0 auto; }
  #faq .dl-horizontal dt {
    white-space: normal; }
  #faq .omacenter {
    margin-left: auto;
    margin-right: auto; }
