/* Custom dropdown */
.custom-dropdown {
  position: relative;
  display: inline-block;
  vertical-align: middle; }

.custom-dropdown select {
  height: 40px;
  background: white;
  padding: 7px;
  font-size: inherit;
  border: 0;
  margin: 0;
  width: 115px;
  border-radius: 3px;
  text-indent: 0.01px;
  text-overflow: '';
  -webkit-appearance: button;
  /* hide default arrow in chrome OSX */ }

.custom-dropdown::before,
.custom-dropdown::after {
  content: "";
  position: absolute;
  pointer-events: none; }

.custom-dropdown::after {
  /*  Custom dropdown arrow */
  content: "\25BC";
  height: 1em;
  font-size: .625em;
  line-height: 1;
  right: 1.2em;
  top: 50%;
  margin-top: -.5em; }

.custom-dropdown::before {
  width: 2em;
  right: 0;
  top: 0;
  bottom: 0; }

.custom-dropdown::before {
  background-color: white;
  color: white; }

