.find {
  background-repeat: no-repeat;
  background-color: #B6B2AB;
  background-image: -webkit-linear-gradient(-405deg, #B6B2AB 0%, #B3AFA7 25%, #B8B5AF 25%, #78736B 50%, #6F6862 50%, #58504B 75%, #5F574E 75%, #625A51 100%);
  background-image: -webkit-linear-gradient(315deg, #B6B2AB 0%, #B3AFA7 25%, #B8B5AF 25%, #78736B 50%, #6F6862 50%, #58504B 75%, #5F574E 75%, #625A51 100%);
  background-image: linear-gradient(135deg, #B6B2AB 0%, #B3AFA7 25%, #B8B5AF 25%, #78736B 50%, #6F6862 50%, #58504B 75%, #5F574E 75%, #625A51 100%);
  padding: 85px;
  padding-bottom: 15px;
  margin-top: 200px; }
  .find a:-webkit-any-link {
    color: lightseagreen;
    text-decoration: none; }
  .find .links {
    color: lightcyan;
    text-align: center; }
  .find .fa {
    color: lightseagreen;
    padding-left: 0; }
  .find .container {
    width: 100%; }
  .find .card-container {
    -webkit-perspective: 800px;
    -moz-perspective: 800px;
    -o-perspective: 800px;
    perspective: 800px;
    margin-bottom: 30px; }
  .find .front {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -o-backface-visibility: hidden;
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #FFF;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.14); }
  .find .card {
    background: none repeat scroll 0 0 #FFFFFF;
    border-radius: 4px;
    color: #444444; }
  .find .card-container, .find .front, .find .back {
    width: 100%;
    height: 500px; }
  .find .card .cover {
    background: #373746;
    height: 105px;
    overflow: hidden; }
  .find .card .user {
    border-radius: 50%;
    display: block;
    height: 120px;
    margin: -55px auto 0;
    overflow: hidden;
    width: 120px; }
  .find .card .user img {
    background: none repeat scroll 0 0 #FFFFFF;
    border: 4px solid #FFFFFF;
    width: 100%; }
  .find .card .content {
    background-color: transparent;
    box-shadow: none;
    padding: 10px 20px 20px; }
  .find .card .name {
    font-size: 22px;
    line-height: 28px;
    margin: 10px 0 0;
    text-align: center;
    text-transform: capitalize; }
  .find .card .profession {
    color: #999999;
    text-align: center;
    margin-bottom: 20px; }
  .find .title {
    color: lightcyan;
    text-align: center;
    font-weight: 300;
    font-size: 44px;
    margin-bottom: 90px;
    line-height: 90%; }
  .find .social-links {
    margin-top: 9px;
    justify-content: center;
    display: flex;
    list-style: none; }
    .find .social-links li i {
      line-height: 0;
      color: black;
      background: white !important; }
  .find .contact-form h1 {
    color: white;
    text-align: center; }
  .find .contact-form h3 {
    color: lightcyan;
    line-height: 1.5; }
  .find .contact-form input:focus ~ label, .find .contact-form textarea:focus ~ label, .find .contact-form input:valid ~ label, .find .contact-form textarea:valid ~ label {
    font-size: 0.75em;
    color: #999;
    top: -5px;
    -webkit-transition: all 0.225s ease;
    transition: all 0.225s ease; }
  .find .contact-form .styled-input {
    float: left;
    width: 293px;
    margin: 1rem 0;
    position: relative;
    border-radius: 4px; }
  @media only screen and (max-width: 768px) {
    .find .contact-form .styled-input {
      width: 100%; } }
  .find .contact-form .styled-input label {
    color: #999;
    padding: 1.3rem 30px 1rem 30px;
    position: absolute;
    top: 10px;
    left: 0;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    pointer-events: none; }
  .find .contact-form .styled-input.wide {
    width: 650px;
    max-width: 100%; }
  .find .contact-form input,
  .find .contact-form textarea {
    padding: 30px;
    border: 0;
    width: 100%;
    background-color: snow;
    color: black; }
  .find .contact-form input:focus,
  .find .contact-form textarea:focus {
    outline: 0; }
  .find .contact-form input:focus ~ span,
  .find .contact-form textarea:focus ~ span {
    width: 100%;
    -webkit-transition: all 0.075s ease;
    transition: all 0.075s ease; }
  .find .contact-form textarea {
    width: 100%;
    min-height: 15em; }
  .find .contact-form .input-container {
    width: 650px;
    max-width: 100%;
    margin: 20px auto 25px auto; }
  @media (max-width: 768px) {
    .find .contact-form .submit-btn {
      width: 100%;
      float: none;
      text-align: center; } }
  .find .contact-form input[type=checkbox] + label {
